.a{
    height: 100vh;
    display: flex;
    align-items: center;
}

.a-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    /* background-color: red; */
    height: 100%;
}

.a-right{
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-right: 100px;
    text-align: justify;
    /* background-color: rebeccapurple; */
    height: 100%;
}

.a-card.bg{
    /* If use position: absolute then parent should be position: relative */
    position: absolute;
    top: 50px;
    left: 50px;
    background-color: #333;
}

.a-card{
    width: 60%;
    height: 70vh;
    border-radius: 30px;
    position: absolute;
    overflow: hidden;
}

.a-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.a-title{
    margin-top: 5rem;
    font-size: 50px;
}

.a-salutation{
    margin-top: 2rem;
    margin-bottom: 2rem;
    font-size: 30px;
    font-weight: 400;
}

.a-sub{
    margin-top: 0.5rem;
    margin-bottom: 1rem;
}

.a-desc{
    margin-top: 0;
    margin-bottom: 1rem;
}

.a-right li{
    margin-left: 50px;
    list-style-type: circle;
    display: list-item;
}

@media screen and (max-width: 480px){
    .a{
        flex-direction: column;
        text-align: center;
        margin-top: 30px;
    }

    .a-card, .a-card.bg{
        display: none;
    }

    .a-title{
        font-size: 40px;
    }

    .a-salutation{
        margin-top: 1rem;
    }

    .a-right{
        width: 80%;
        margin-left: 100px;
        line-height: 2;
    }

    .a-right li{
        margin-left: 25px;
        margin-bottom: 10px;
    }
}